import React, { useState, useEffect } from "react";
import * as XLSX from "xlsx"; // For reading Excel files
import "./Mockup.css";
import { Form, Stack } from "react-bootstrap";
import CustomButton from "../../components/Button/Button";
import FileUpload from "../../components/FileUploader/FileUploader";
import CustomModal from "../../components/Modal/customModal";
import { downloadTemplate } from "../../utils/helpers";
import useToast from "../../hooks/useToast";
import config from "../../utils/config.json"; // ✅ Import config.json

const MassUpload = () => {
  const [modalShow, setModalShow] = useState(false);
  const [operationChoice, setOperationChoice] = useState("");
  const [uploadedData, setUploadedData] = useState(null);
  const [errors, setErrors] = useState([]);
  const { showErrorToast } = useToast();

  useEffect(() => {
    console.log("🔄 Config loaded:", config);
  }, []);

  // Read XLSX and validate
  const handleFileUpload = (files) => {
    if (files.length === 0) {
      console.warn("⚠️ No file selected.");
      return;
    }

    const file = files[0];
    console.log("📄 Reading file:", file.name);

    const reader = new FileReader();

    reader.onload = (e) => {
      console.log("✅ File successfully read.");

      const data = new Uint8Array(e.target.result);
      const workbook = XLSX.read(data, { type: "array" });

      const sheetName = workbook.SheetNames[0]; // ✅ Ensure the first sheet is selected
      console.log("📜 Sheet Name:", sheetName);

      const sheet = workbook.Sheets[sheetName];

      if (!sheet) {
        console.error("❌ No valid sheet found in the uploaded file.");
        return;
      }

      const jsonData = XLSX.utils.sheet_to_json(sheet, { defval: "" }); // ✅ Ensures empty cells are included
      console.log("📊 Parsed XLSX Data:", jsonData);

      if (jsonData.length === 0) {
        console.warn("⚠️ The sheet is empty or not formatted correctly.");
        return;
      }

      // Validate data before setting state
      const missingFields = validateData(jsonData, config.mandatoryFields);
      if (missingFields.length > 0) {
        console.error("❌ Missing Mandatory Fields:", missingFields);
        setErrors(missingFields);
        setModalShow(true);
        return;
      }

      setUploadedData(jsonData);
      setErrors([]); // Reset errors if valid
    };

    reader.readAsArrayBuffer(file);
  };

  // Validate required fields in XLSX
  const validateData = (data, mandatoryFields) => {
    console.log("🔍 Validating uploaded data...");
    let missingFields = [];

    data.forEach((row, index) => {
      mandatoryFields.forEach((field) => {
        if (!row[field] || row[field].toString().trim() === "") {
          missingFields.push(`Row ${index + 1}: ${field} is missing`);
        }
      });
    });

    console.log("🔎 Validation complete. Missing fields:", missingFields);
    return missingFields;
  };

  const handleDownloadTemplate = () => {
    console.log("⬇️ Downloading template for operation:", operationChoice);
    downloadTemplate(showErrorToast)(operationChoice);
  };

  const handleSubmit = () => {
    if (!uploadedData) {
      showErrorToast("❌ No file uploaded. Please upload an Excel file.");
      return;
    }

    if (errors.length > 0) {
      showErrorToast("⚠️ Cannot submit! Fix errors first.");
      setModalShow(true);
      return;
    }

    console.log("✅ Data is valid. Submitting...");
    // Proceed with API submission or next steps
  };

  return (
    <>
      <Stack className="mt-3" gap={4}>
        {/* Choose operation and download template */}
        <Stack direction="horizontal">
          <Form.Select
            aria-label="Choose Operations"
            style={{
              width: "30%",
              border: "1px solid #007bff",
              borderRadius: "8px",
            }}
            onChange={(e) => {
              console.log("📌 Selected Operation:", e.target.value);
              setOperationChoice(e.target.value);
            }}
          >
            <option value="">Choose Operations</option>
            <option value="1">Physical Product/Raw Material</option>
            <option value="2">Physical Product Structure</option>
            <option value="3">Document</option>
            <option value="4">Physical Product-Document</option>
          </Form.Select>

          <CustomButton
            variant="link ms-auto btn-lg"
            onClick={handleDownloadTemplate}
            text="Download Template"
          />
        </Stack>

        {/* Drag and drop container */}
        <FileUpload
          fileTypes={["XLSX"]}
          multiple={false}
          onUpload={handleFileUpload}
        />

        {/* Form-CTA submit */}
        <Stack direction="horizontal">
          <Form.Check type="checkbox" label="background" />
          <CustomButton
            variant="primary ms-auto"
            text="Submit"
            onClick={handleSubmit}
          />
        </Stack>
      </Stack>

      {/* Modal to show missing fields */}
      <CustomModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        title="Missing Mandatory Fields"
      >
        {errors.length > 0 ? (
          <ul>
            {errors.map((error, idx) => (
              <li key={idx} style={{ color: "red" }}>
                {error}
              </li>
            ))}
          </ul>
        ) : (
          <p>No errors found</p>
        )}
      </CustomModal>
    </>
  );
};

export default MassUpload;
